import axios from 'axios'
import { useUserStore } from '@/store/user'
import router from '@/router'
import { ElMessage } from 'element-plus'

const userStore = useUserStore()

const service = axios.create({
  baseURL: process.env.VUE_APP_BASE_APT
})

//  发送请求前拦截
service.interceptors.request.use(config => {
  config.headers.Authorization = `USER_TOKEN=${userStore.userToken}`
  if (config.url === '/api/user/register' || config.url === '/api/user/emailLogin') {
    config.headers.emailsession = userStore.emailSession
  }
  return config
}, rej => {
  return Promise.reject(rej)
})

//  返回响应后拦截
service.interceptors.response.use(response => {
  if (response.headers.authorization) {
    userStore.updateUserToken(response.headers.authorization.split('=')[1])
  }

  if (response.headers.emailsession) {
    userStore.emailSession = response.headers.emailsession
  }
  if (response.data.code === 403) {
  //  token异常
    ElMessage({
      type: 'error',
      message: '登录过期'
    })
    router.push('/')
  }

  return response.data
}, err => {
  if (err.status === 500) {
    ElMessage({
      type: 'error',
      message: 'error code 500'
    })
  }
  return Promise.reject(err)
})

export default service
